<template>
  <div class="page-wrapper buses-page">
    <!-- App Header -->
    <AppHeader layoutType="layout-2"></AppHeader>

    <section class="viewport-h-min-100">
      <!-- Include AppLoader (Show Progress On Page) -->
      <div class="container p-3 mobile-p-1" style="background: #fff;">
        <div class="p-t-1 p-b-1">
          <h1 class="title font-bold font-h-2 mr-b-sm">Your Trip Review</h1>
          <p
            class="text"
          >For {{ selected_seats.length }} passenger(s) - including airfare, taxes, fees and carrier-imposed charges</p>
        </div>

        <div
          class="title-wrapper with-icon pos-relative flexbox justify-center align-items-center p-t-xs p-b-xs mr-t-2 mr-b-2 mobile-mr-t-1 mobile-mr-b-1"
        >
          <span class="p-l-2 p-r-2 mobile-p-l-0 mobile-p-r-0 align-items-center zindex-5 bg-white">
            <img src="@/assets/icons/bus-icon-blue.svg" alt="Book Bus" />
            <h3
              class="title font-h-2 mobile-font-sm"
            >Leaving from {{ trip.town_from.town_name }} to {{ trip.town_to.town_name }}</h3>
          </span>
        </div>

        <div class="mr-b-1">
          <h3 class="title font-h-2">{{ trip.travelling_on | formatDate }}</h3>
        </div>

        <div class="trip-review-container">
          <div
            class="bus-list-card-container pos-relative p-t-1 p-b-1"
            v-for="(selected_seat, index) in selected_seats"
            :key="index"
          >
            <div class="bus-card-wrapper flexbox pos-relative mobile-flex-wrap">
              <div class="bus-info">
                <div class="header p-1 flexbox justify-between color-secondary font-bold font-xs">
                  <div class="bus-type">{{ trip.selected_bus.bus_type_name }} Bus</div>
                </div>
                <div class="content p-1">
                  <div
                    class="row flexbox justify-between align-items-center color-seondary font-sm font-regular"
                  >
                    <span>{{ trip.town_from.town_name }}</span>
                    <span>{{ trip.town_to.town_name }}</span>
                  </div>
                  <div
                    class="row flexbox justify-between align-items-center color-seondary font-sm font-regular"
                  >
                    <span>Departure Time</span>
                    <span>{{ trip.selected_bus.latest_departure_time }}</span>
                  </div>
                </div>
              </div>
              <div class="bus-info">
                <div class="header p-1 flexbox justify-between color-secondary font-bold font-xs">
                  <div class="bus-type">Selected seat</div>
                </div>
                <div class="content p-1">
                  <div
                    class="row flexbox justify-between align-items-center color-seondary font-sm font-regular"
                  >
                    <span>{{ selected_seat.seat_number }}</span>
                  </div>
                </div>
              </div>
              <div class="bus-price-wrapper">
                <a
                  href="#"
                  class="price-label flex-1 flexbox flex-dir-col justify-center align-items-center p-1 active"
                >
                  <div>
                    <span class="mr-b-sm">{{ selected_seat.seat_type }}</span>
                    <span class="mr-b-sm">
                      <em class="currency">KES</em>
                      {{ (selected_seat.price.price_kes_amount - bus_seats.online_discount.KES) | formatCurrency }}
                    </span>
                  </div>
                </a>
              </div>
            </div>
          </div>
        </div>

        <!-- Returning-->
        <div
          class="title-wrapper with-icon pos-relative flexbox justify-center align-items-center p-t-xs p-b-xs mr-t-2 mr-b-2 mobile-mr-t-1 mobile-mr-b-1"
          v-if="return_selected_seats.length > 0"
        >
          <span class="p-l-2 p-r-2 mobile-p-l-0 mobile-p-r-0 align-items-center zindex-5 bg-white">
            <img src="@/assets/icons/bus-icon-blue.svg" alt="Book Bus" />
            <h3
              class="title font-h-2 mobile-font-sm"
            >Returning from {{ trip.town_from_return.town_name }} to {{ trip.town_to_return.town_name }}</h3>
          </span>
        </div>

        <div class="mr-b-1" v-if="return_selected_seats.length > 0">
          <h3 class="title font-h-2">{{ trip.returning_on | formatDate }}</h3>
        </div>

        <div class="trip-review-container" v-if="return_selected_seats.length > 0">
          <div
            class="bus-list-card-container pos-relative p-t-1 p-b-1"
            v-for="(selected_seat, index) in return_selected_seats"
            :key="index"
          >
            <div class="bus-card-wrapper flexbox pos-relative mobile-flex-wrap">
              <div class="bus-info">
                <div class="header p-1 flexbox justify-between color-secondary font-bold font-xs">
                  <div class="bus-type">{{ trip.return_selected_bus.bus_type_name }} Bus</div>
                </div>
                <div class="content p-1">
                  <div
                    class="row flexbox justify-between align-items-center color-seondary font-sm font-regular"
                  >
                    <span>{{ trip.town_from_return.town_name }}</span>
                    <span>{{ trip.town_to_return.town_name }}</span>
                  </div>
                  <div
                    class="row flexbox justify-between align-items-center color-seondary font-sm font-regular"
                  >
                    <span>Departure Time</span>
                    <span>{{ trip.return_selected_bus.latest_departure_time }}</span>
                  </div>
                </div>
              </div>
              <div class="bus-info">
                <div class="header p-1 flexbox justify-between color-secondary font-bold font-xs">
                  <div class="bus-type">Selected seat</div>
                </div>
                <div class="content p-1">
                  <div
                    class="row flexbox justify-between align-items-center color-seondary font-sm font-regular"
                  >
                    <span>{{ selected_seat.seat_number }}</span>
                  </div>
                </div>
              </div>
              <div class="bus-price-wrapper">
                <a
                  href="#"
                  class="price-label flex-1 flexbox flex-dir-col justify-center align-items-center p-1 active"
                >
                  <div>
                    <span class="mr-b-sm">{{ selected_seat.seat_type }}</span>
                    <span class="mr-b-sm">
                      <em class="currency">KES</em>
                      {{ (selected_seat.price.price_kes_amount - parseInt(return_bus_seats.online_discount.KES) - parseInt(return_bus_seats.return_discount.KES)) | formatCurrency }}
                    </span>
                  </div>
                </a>
              </div>
            </div>
          </div>
        </div>
        <!--End Returning-->
      </div>

      <div class="container p-3 mobile-p-1 bg-secondary">
        <div class="grand-total-wrapper flexbox align-items-center justify-between">
          <h3 class="title text-white font-bold">Grand Total</h3>
          <h3
            class="title text-white font-bold"
          >KES {{ (total_amount + return_total_amount) | formatCurrency }}</h3>
        </div>
      </div>

      <div class="container p-3 mobile-p-0">
        <div class="p-2 mobile-p-1 mobile-p-t-2 mobile-p-b-2">
          <h2 class="title mr-b-1 font-bold">Payment Means</h2>
          <p class="text color-dark font-sm">
            Preferred method of payment is MPESA. Read more about how to pay via M-PESA.
            Please only use English alphabet characters and numbers. The reservations system is unable to recognise non-English characters and symbols
            such as &, >` and ‘the and . ymbol, the less-than symbol, the greater-than symbol and the apostrophe.
          </p>
          <div class="btn-wrapper mr-t-2 p-t-1 flexbox justify-stert align-items-center">
            <button
              class="btn btn-inline mobile-w-100"
              @click.prevent="tripReviewConfirmed"
            >PROCEED TO PAYMENT</button>
          </div>
        </div>
      </div>

      <div class="container p-3 mobile-p-2 bg-white">
        <div class="navigation-wrapper flexbox justify-between align-items-center">
          <router-link to="/Passangers" class="nav-prev flexbox justify-between align-items-center">
            <img
              src="@/assets/icons/arrow-left-icon.svg"
              alt="Arrow"
              class="mr-r-1"
              style="width: 30px"
            />
            <span class="color-primary mobile-hidden">BACK</span>
          </router-link>
          <router-link to="/Payment" class="nav-next flexbox justify-between align-items-center">
            <span class="color-primary mobile-hidden">NEXT</span>
            <img
              src="@/assets/icons/arrow-left-icon.svg"
              alt="Arrow"
              class="mr-l-1 rotated-arrow"
              style="width: 30px"
            />
          </router-link>
        </div>
      </div>
    </section>

    <!-- App Footer -->
    <AppFooter />
  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters } from "vuex";

//AppHeader component
import AppHeader from "@/components/AppHeader.vue";

//AppFooter component
import AppFooter from "@/components/AppFooter.vue";

export default {
  name: "AppTripReview",
  components: {
    AppHeader,
    AppFooter
  },
  methods: {
    tripReviewConfirmed() {
      //show payemnet component
      this.$router.push("Payment");
    }
  },
  computed: mapGetters([
    "trip",
    "selected_seats",
    "return_selected_seats",
    "total_amount",
    "return_total_amount",
    "bus_seats",
    "return_bus_seats"
  ]),
  mounted() {
    //scroll to top
    window.scrollTo(0, 0);
  }
};
</script>


